body {
  margin: 0;
  padding: 0;
  font-family: "Merriweather", "Georgia", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
    display: flex;
    justify-content: center;
}
.App {
    text-align: center;
}

.App-header {
    margin-top: 100px;
    background-color: #fff;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    font-size: calc(10px + 2vmin);
    color: #000;
    width: 700px;
}

.App-link {
    color: #FF2353;
}
.name {
    font-weight: 700;
    font-size: 32px;
}

.link-container {
    display: flex;
    justify-content: space-between;
    width: 350px;
    margin-bottom: 15px;
}

